import React from 'react'
import BannerSlider from '../../Components/BannerSlider'
import Anouncement from '../../Components/Anouncement'
import StatisticBox from '../../Components/StatisticBox'
import map_img from "../../Assets/images/map_real.png";
import gallery_ben from "../../Assets/images/gallery_ben.png";
import WelcomeHomeText from '../../Components/WelcomeHomeText';
import TabContentHome from '../../Components/TabContentHome';
import FaqPage from '../../Components/FaqPage';
import HomeThreeBox from '../../Components/HomeThreeBox';
import scroll_top from "../../Assets/images/scroll_top.png";
import FooterCus from '../../Components/FooterCus';
// import Faq from '../../Components/Faq';

function Home() {
  const stats=[
    {
      title:'Ongoing 2024',
      count:'30',
      class_cus:'bar_green'
    },
    {
      title:'Completed Elections 2024',
      count:'20',
      class_cus:'bar_yellow'
    },
    {
      title:'Due',
      count:'200',
      class_cus:'bar_blue'
    }
  ]

  const bottomThreeBox = [
	{
	title:'Act &amp; Rules',
	description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis ipsum suspendisse ultrices gravida.',
	pageLink:'',
	class_cus:'box_a'
	},
	{
	title:'Statistics',
	description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis ipsum suspendisse ultrices gravida.',
	pageLink:'',
	class_cus:'box_b'
	},
	{
	title:'RTI (Right To Information)',
	description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis ipsum suspendisse ultrices gravida.',
	pageLink:'',
	class_cus:'box_c'
	}
  ]
  return (
    <>
      <BannerSlider/>
      
      <div className="wrapper">
      <div className="col-sm-12">
      <Anouncement/>
      </div>	
      </div>

      <div className="body_custom" id="main_info_content">

      <div className="wrapper map_sec_main">
	<div className="col-sm-12">
	<div className="map_sec">
	<div className="col-sm-5 float-left map_left_sec">

	<h2>Statistic</h2>
	{stats.map(item=>
  <StatisticBox 
  title={item.title}
  count={item.count}
  class_cus={item.class_cus}
  />
  )}

	<div className="btn_sec">
		<a href="#">View All</a>
	</div>
	
	</div>
	<div className="col-sm-7 float-left">
	<div className="right_map"><img src={`${map_img}`} alt=""/></div>
	</div>
	</div>
	</div>
	</div>

  <div className="welcomeSec">
  <div className="wrapper">
  <div className="welcomeSecLeft">
  <WelcomeHomeText/>
  </div>
  <div className="welcomeSecRight">

  <h2><i className="fa fa-file-image-o" aria-hidden="true"></i> Gallery <a href="#">View All</a></h2>
  <img src={`${gallery_ben}`} alt=""/>
  </div>
  </div>
  </div>

  <TabContentHome/>

<div class="faqSec">
	
	<div class="section-header text-center">
	
	<h2 class="title">FAQs</h2>
	<p class="style-two-des pp">Discover answers to all your questions and enhance your understanding to make smarter decisions on saving, investing, and spending. Explore all queries here for valuable insights!</p>
	</div>
	
	<div class="faqs-section">
	<div class="container">
		<div class="row d-flex justify-content-center">
		<div class="col-sm-10">
		<FaqPage iconPosition={'end'}/>

    <div class="btn_sec"><a href="#">View All</a></div>

		</div>
		</div>
	</div>
	</div>
	
</div>


<div class="three_box">
	<div class="wrapper_sm">
    

	{bottomThreeBox.map(item =>
	<div class="col-sm-4 float-left">
	<HomeThreeBox
	title={item.title}
	description= {item.description}
	pageLink= {item.pageLink}
	class_cus= {item.class_cus}
	/>
	</div>
	)}
		
	</div>
	</div>

<FooterCus/>

  </div>
      

    </>
  )
}

export default Home