import React from 'react'

function SearchBox() {
  return (
    <>
    <div className="search_box">
		<h2>Search</h2>
		<label><select>
		  <option value="option 1">Select District</option>
		  <option value="option 2">Select District</option>
		</select>
		</label>
			
		<label><select>
		  <option value="option 1">Select Range</option>
		  <option value="option 2">Select Range</option>
		</select>
		</label>
			
		<label><select>
		  <option value="option 1">Select Type</option>
		  <option value="option 2">Select Type</option>
		</select>
		</label>
			
		<label>
			{/* <input name='Society_Name' type="text" placeholder="Society Name"> */}
            <input
            type="text"
            name="Society_Name"
            // value={password}
            // onChange={(text) => setNewPass(text.target.value)}
             placeholder="Society Name"
          />
		</label>
		
		<label className="btn">
		{/* <input type="submit" value="Search"> */}

        <button
          type="submit"
        //   disabled={disable}
        //   onClick={() => {
        //     changePass();
        //   }}
        className="">Search</button>
		</label>
			
			
		</div>
    </>
  )
}

export default SearchBox